

header{

     
    #burger, [for=burger]{
        // display: none;
        visibility: hidden;
    }
        
        
    @media screen and (max-width:550px){
        
        .burg{
            // display: block;
            visibility: visible;
            position: absolute;
            width: 30px;
            text-align: center;
            right: 33px;
            top: 27px;
    
            &::before{
                content: "\2630";
                font-size: 30px;
                
                
            }
    
           
        }
    
    
        ul{
            position: absolute;
            top: 70px;
            right: 150px;
            background-color: rgb(67, 148, 219);
           border-radius: 5px;
            height: 10.5rem;
            padding-right: 2rem;
    
            
        }
    
        
    
        .items{
    
           position: absolute;
           top: 130px;
           left: -60px;
        
           
        }
    
        #burger:checked + label + ul{
            display: block;
            animation: action .7s ease;
        }
    
        #burger:checked +[for=burger]::before{
            content: "\00d7";
            font-size: 30px;
            border: 2px solid;
            background-color: aquamarine;
            
            border-radius: 50%;
            
            display: block;
            width: 50px;
            height: 50px;
            animation: spin 1s linear infinite;
        }
    
        @keyframes action {
            0%{
                opacity: 0;
            }
            100%{
                opacity: 1;
            }
        }
        @keyframes spin {
            from{
                transform: rotate(0deg);
            }
            to{
                transform: rotate(360deg);
            }
        }
    }
    
    
    }

main{
    .pouring{
        padding: 2rem 0;
        font-size: 50px;
        line-height: 70px;
        font-family: Verdana, sans-serif;
        font-weight: 1000;
        position: relative;
        background-color: #ffff;
        overflow: hidden;
        text-transform: uppercase;
        text-align: center;

         &:before {
            content: '';
            position: absolute;
            filter: blur(10px);
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            mix-blend-mode: screen;
            background-image: repeating-linear-gradient(-45deg, transparent, transparent 1em, #BFE2FF 1em, #337AB7 50%), repeating-linear-gradient(45deg, #337AB7, #337AB7 1em, #FFF 1em, #BFE2FF 50%);
            background-size: 3em 3em, 2em 2em;
            animation-name: ani;
            animation-duration: 10s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;
            animation-direction: alternate;
        }
        @keyframes ani {
            from {
                background-position: 0 0;
            }
            to {
                background-position: 100% 0;
            }
        }
        @media (max-width:690px) {    
            .pouring {font-size: 20px;}    
        }
    }

    padding-top: 95px;
    .card{
        img{
            height: 30rem;
        }

        .card-body{

            .card-title{
                margin-top: 1rem;
            }
            p{
                padding: 1rem 0;
                line-height: 1.7em;
                font-size: 1.3rem;
              }

              .card-text{
                min-height: 58rem;
              }
        }

       
    }
}