header #burger, header [for="burger"] {
  visibility: hidden;
}

@media screen and (max-width: 550px) {
  header .burg {
    visibility: visible;
    width: 30px;
    text-align: center;
    position: absolute;
    top: 27px;
    right: 33px;
  }

  header .burg:before {
    content: "☰";
    font-size: 30px;
  }

  header ul {
    height: 10.5rem;
    background-color: #4394db;
    border-radius: 5px;
    padding-right: 2rem;
    position: absolute;
    top: 70px;
    right: 150px;
  }

  header .items {
    position: absolute;
    top: 130px;
    left: -60px;
  }

  header #burger:checked + label + ul {
    animation: action .7s;
    display: block;
  }

  header #burger:checked + [for="burger"]:before {
    content: "×";
    width: 50px;
    height: 50px;
    background-color: #7fffd4;
    border: 2px solid;
    border-radius: 50%;
    font-size: 30px;
    animation: spin 1s linear infinite;
    display: block;
  }

  @keyframes action {
    0% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }

  @keyframes spin {
    from {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(360deg);
    }
  }
}

main {
  padding-top: 95px;
}

main .pouring {
  text-transform: uppercase;
  text-align: center;
  background-color: #fff;
  padding: 2rem 0;
  font-family: Verdana, sans-serif;
  font-size: 50px;
  font-weight: 1000;
  line-height: 70px;
  position: relative;
  overflow: hidden;
}

main .pouring:before {
  content: "";
  filter: blur(10px);
  width: 100%;
  height: 100%;
  mix-blend-mode: screen;
  background-image: repeating-linear-gradient(-45deg, #0000, #0000 1em, #bfe2ff 1em, #337ab7 50%), repeating-linear-gradient(45deg, #337ab7, #337ab7 1em, #fff 1em, #bfe2ff 50%);
  background-size: 3em 3em, 2em 2em;
  animation-name: ani;
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes ani {
  from {
    background-position: 0 0;
  }

  to {
    background-position: 100% 0;
  }
}

@media (max-width: 690px) {
  main .pouring .pouring {
    font-size: 20px;
  }
}

main .card img {
  height: 30rem;
}

main .card .card-body .card-title {
  margin-top: 1rem;
}

main .card .card-body p {
  padding: 1rem 0;
  font-size: 1.3rem;
  line-height: 1.7em;
}

main .card .card-body .card-text {
  min-height: 58rem;
}

